import Vue from 'vue'
import Vuex from 'vuex'
import moment from "moment"

Vue.use(Vuex)
// const base = (process.env.NODE_ENV == "development") ? "/" : process.env.BASE_URL || "/";
export default new Vuex.Store({
  state: {
    dataFiles:[
      {
        "name":"data",
        "url":{
          "all":"https://packetlab.web.illinois.edu/brief_data.jsonl",
          "week":"https://packetlab.web.illinois.edu/brief_data_week.jsonl",
          "24h":"https://packetlab.web.illinois.edu/brief_data_24h.jsonl",
        },
        "format":"jsonl"
      },
      /* {
        "name":"experiments",
        "url":{'all':base + "data/experiments.jsonl"},
        "format":"jsonl"
      }, */
      // {
      //   "name":"experimentDescriptions",
      //   "url":{'all':"https://packetlab.web.illinois.edu/experiments.jsonl"},
      //   "format":"jsonl"
      // },
      {
        "name":"endpoints",
        "url":{'all':"https://packetlab.web.illinois.edu/endpoints.jsonl"},
        "format":"jsonl"
      }
    ],
    "dataLoadedTimeframes":[
      {
        'name':'24h',
        // Number of seconds before next breakpoint
        'cutoff':86400,
        'loading':false,
        "loaded":false,
      },
      {
        'name':'week',
        'cutoff':604800,
        'loading':false,
        "loaded":false,
      },
      {
        'name':'all',
        'cutoff':Number.MAX_VALUE,
        'loading':false,
        'loaded':false,
      },
    ],
    "data":null,
    "experimentDescriptions":null,
    "experiments":null,
    "endpoints":null,
    "navHidden":false,
  },
  mutations: {
    setData(state,dataMessage){
        const data = dataMessage.data;
        const timeframe = dataMessage.timeframe;
        state.dataLoadedTimeframes.forEach((range)=>{
          if(range.cutoff <= timeframe.cutoff){
            range.loaded = true;
          }
        })
        Object.keys(data).forEach((key)=>{
          state[key] = data[key]
        });
    },
    setNavHidden(state, hidden){
      state.navHidden = hidden;
    }
  },
  actions: {
    loadData({commit,state}, fromTime){
        console.time("Data Load Started")
        const currentTime = moment().format("X")
        const deltaTime = currentTime-fromTime;
        const timeframe = state.dataLoadedTimeframes.find((range)=>deltaTime<=range.cutoff);
        return new Promise((resolve,reject)=>{
            if(!timeframe.loaded){
                if(!timeframe.loading){
                    timeframe.loading = true;
                    const worker = new Worker("workers/fetch-data.worker.js");    
                    worker.addEventListener("message",function(e){
                        console.timeEnd("Data Load Started");
                        worker.terminate();
                        const data = e.data;
                        if(data.status=="finished"){
                            commit('setData',{
                              "data":data.message,
                              "timeframe":timeframe
                            });
                            resolve();
                        }else{
                            reject();
                        }
                    });
                    const timeframeDataFiles = {}
                    state.dataFiles.forEach((file)=>{
                        for(let i=0; i<state.dataLoadedTimeframes.length; i++){
                            const range = state.dataLoadedTimeframes[i];
                            if(range.cutoff >= timeframe.cutoff && range.name in file.url){
                                timeframeDataFiles[file.name] = {
                                    url:file.url[range.name],
                                    format:file.format,
                                }
                                break;
                            }
                        }
                    })
                    console.log(timeframeDataFiles);
                    worker.postMessage({"status":"load","url":timeframeDataFiles})
                }else{
                    let pollingInterval;
                    pollingInterval = setInterval(()=>{
                        console.log("running");
                        if(timeframe.loaded){
                            clearInterval(pollingInterval);
                            resolve();
                        }
                    },100);
                }
            }else{
                resolve();
            }
        })
    }
  },
  modules: {
  }
})
